@import '../moment.module.scss';
$color:#2775ff;
.head{
    &_ul{
        @include Line(50px);
        background: $color;
        margin: 0;
        @include Display('left');
        &_li{
            padding: 0 50px 0 0;
            a{
                color: #fff;
                cursor: pointer;
            }
        }
    }
}